<template>
  <div style="width:95vw;" class="mb40">
    <ui-slider class="mt10" showMarker="true" min="0" max="500" v-model="time"></ui-slider>
    <div class="body df aic jcsb">
      <div class="code br5">
        <codemirror style="height:100%;" v-model="viewCode" :options="cmOptions"></codemirror>
      </div>
      <div class="html br5">
        <iframe style="height:100%;width:100%;border:0;" :srcdoc="viewHtml"></iframe>
      </div>
    </div>
  </div>
</template>

<script>
import { codemirror } from "vue-codemirror";

require("codemirror/mode/javascript/javascript.js");
require("codemirror/mode/clike/clike.js");
require("codemirror/mode/shell/shell.js");
//theme ------------------css
require("codemirror/theme/blackboard.css");
//
require("codemirror/addon/hint/show-hint.css");
require("codemirror/addon/hint/show-hint.js");
//
require("codemirror/addon/hint/javascript-hint.js");
require("codemirror/addon/hint/html-hint");
require("codemirror/addon/hint/css-hint");

// import 'codemirror/addon/scroll/simplescrollbars.css'
// import 'codemirror/addon/scroll/simplescrollbars'  //

// import 'codemirror/addon/hint/xml-hint'
// import 'codemirror/addon/hint/anyword-hint'
import { UiSlider } from "keen-ui";
export default {
  name: "CssCode",
  components: { UiSlider, codemirror },
  props: {
    html: {
      type: Array,
      default() {
        return [];
      }
    },
    time: {
      type: Number,
      default() {
        return 250;
      }
    }
  },
  data() {
    return {
      viewCode: "    <!------ Ctrl+Q   ------>", //
      lscode: "", //
      viewHtml: "", //
      lshtml: [], //
      cmOptions: {
        tabSize: 4, //4
        //mode: "htmlmixed",
        //mode: "text/javascript", //javascript
        theme: "blackboard", //
        smartIndent: true, // 
        lineNumbers: true, //
        matchBrackets: true, //
        autoCloseTags: true, // 
        autoCloseBrackets: true, // 
        foldGutter: true, // 
        cursorHeight: 0.85, // 
        readOnly: "nocursor", //
        extraKeys: { "Ctrl-Q": "autocomplete" } //
        //scrollbarStyle:"simple"
      }
    };
  },
  watch: {
    html(html) {
      this.lshtml = JSON.parse(JSON.stringify(html));
      this.htmlChange();
    },
    viewCode() {
      if (this.cmOptions.readOnly || this.cmOptions.readOnly == "nocursor")
        return;
      this.viewHtml = this.viewCode;
    }
  },
  mounted() {},
  methods: {
    //html
    htmlChange() {
      if (this.lshtml.length == 0) {
        this.cmOptions.readOnly = false;
        return;
      }
      //this.viewCode.push(""); //
      this.lscode = JSON.parse(JSON.stringify(this.lshtml[0]));
      this.viewCode = this.viewCode + "\n";
      this.codeChange();
      this.lshtml.splice(0, 1); //html
    },
    codeChange() {
      setTimeout(() => {
        this.viewCode = this.viewCode + this.lscode.substr(0, 1);
        this.lscode = this.lscode.substr(1);
        if (this.lscode.length != 0) {
          this.codeChange();
        } else {
          this.viewHtml = this.viewCode;
          this.htmlChange();
        }
      }, this.time);
    }
  }
};
</script>

<style >
.CodeMirror {
  height: 100%;
}

.body > div {
  margin-top: 10px;
  width: 48%;
  height: 90vh;
  border: 1px #fff solid;
  overflow: auto;
}
.html {
  background: #fff;
}
.code * {
  margin: 0;
  padding: 0;
  color: rgb(222, 222, 222);
}
/* .lscode {
  white-space: pre-wrap;
  word-break: break-all;
  text-indent: -2em;
  padding-left: 2.5em;
} */
/* .lscode span[value*="@"],
.lscode span[value*=""],
.lscode span[value*="`"],
.lscode span[value*=""],
.lscode span[value*=":"],
.lscode span[value*=";"],
.lscode span[value*="'"],
.lscode span[value*="."],
.lscode span[value*="-"],
.lscode span[value*=","],
.lscode span[value*="#"],
.lscode span[value*="*"],
.lscode span[value*='"'] {
  color: rgb(42, 161, 152);
}

.lscode span[value*="<"],
.lscode span[value*=">"],
.lscode span[value*="("],
.lscode span[value*=")"],
.lscode span[value*="{"],
.lscode span[value*="}"] {
  color: yellow;
}

.lscode span[value*="a"],
.lscode span[value*="b"],
.lscode span[value*="c"],
.lscode span[value*="d"],
.lscode span[value*="e"],
.lscode span[value*="f"],
.lscode span[value*="g"],
.lscode span[value*="h"],
.lscode span[value*="i"],
.lscode span[value*="j"],
.lscode span[value*="k"],
.lscode span[value*="l"],
.lscode span[value*="m"],
.lscode span[value*="n"],
.lscode span[value*="o"],
.lscode span[value*="p"],
.lscode span[value*="q"],
.lscode span[value*="r"],
.lscode span[value*="s"],
.lscode span[value*="t"],
.lscode span[value*="u"],
.lscode span[value*="v"],
.lscode span[value*="w"],
.lscode span[value*="x"],
.lscode span[value*="y"],
.lscode span[value*="z"],
.lscode span[value*="A"],
.lscode span[value*="B"],
.lscode span[value*="C"],
.lscode span[value*="D"],
.lscode span[value*="E"],
.lscode span[value*="F"],
.lscode span[value*="G"],
.lscode span[value*="H"],
.lscode span[value*="I"],
.lscode span[value*="J"],
.lscode span[value*="K"],
.lscode span[value*="L"],
.lscode span[value*="M"],
.lscode span[value*="N"],
.lscode span[value*="O"],
.lscode span[value*="P"],
.lscode span[value*="Q"],
.lscode span[value*="R"],
.lscode span[value*="S"],
.lscode span[value*="T"],
.lscode span[value*="U"],
.lscode span[value*="V"],
.lscode span[value*="W"],
.lscode span[value*="X"],
.lscode span[value*="Y"],
.lscode span[value*="Z"] {
  color: rgb(133, 153, 0);
  text-indent: 0.15em;
}
.lscode span[value*="0"],
.lscode span[value*="1"],
.lscode span[value*="2"],
.lscode span[value*="3"],
.lscode span[value*="4"],
.lscode span[value*="5"],
.lscode span[value*="6"],
.lscode span[value*="7"],
.lscode span[value*="8"],
.lscode span[value*="9"] {
  color: rgb(187, 137, 0);
  text-indent: 0.15em;
} */
</style>


